  align-items: center;
  gap: 0.5rem;
  height: 120px;
  transition: all 0.2s;
}

.upload-options button:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-sm);
}

.upload-options button i {
  font-size: 2rem;
  color: var(--primary-color);
}

.preview-image {
  margin-top: 1rem;
  position: relative;
  min-height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--bg-color);
  border-radius: var(--radius);
}

.preview-image img {
  max-width: 100%;
  max-height: 400px;
  object-fit: contain;
  border-radius: var(--radius);
  box-shadow: var(--shadow-sm);
}

#remove-photo {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  backdrop-filter: blur(5px);
  background: rgba(255, 255, 255, 0.8);
}

.map-preview {
  height: 300px;
  border-radius: var(--radius);
  margin-top: 1rem;
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-color);
  background-color: var(--bg-color);
}

.camera-modal {
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
}

.camera-modal .modal-content {
  width: 95%;
  max-width: 800px;
  background: white;
  border-radius: var(--radius-lg);
  overflow: hidden;
}

#camera-view {
  width: 100%;
  height: 60vh;
  background: #000;
  display: block;
}

@media (max-width: 768px) {
  .add-story-header h1 {
    font-size: 2rem;
  }
  
  .upload-options {
    grid-template-columns: 1fr;
  }
}

.custom-marker-container {
  background: transparent !important;
  border: none !important;
}

.custom-marker {
  position: relative;
  width: 30px;
  height: 42px;
}

.marker-pin {
  position: absolute;
  width: 30px;
  height: 30px;
  background: var(--primary-color);
  border-radius: 50% 50% 50% 0;
  transform: rotate(-45deg);
  left: 0;
  top: 0;
  opacity: 0.8;
}

.marker-dot {
  position: absolute;
  width: 16px;
  height: 16px;
  background: white;
  border-radius: 50%;
  left: 7px;
  top: 7px;
  transform: rotate(45deg);
}

.leaflet-div-icon {
  background: transparent !important;
  border: none !important;
}

.leaflet-popup-content {
  min-width: 200px;
  font-size: 14px;
}

.leaflet-popup-content a {
  color: var(--primary-color);
  text-decoration: none;
}

.leaflet-popup-content a:hover {
  text-decoration: underline;
}

.layer-control {
  margin-top: 1rem;
}

.leaflet-control-layers {
  border-radius: var(--radius);
  box-shadow: var(--shadow-sm) !important;
  background: white !important;
  padding: 0.5rem !important;
}

.leaflet-control-layers-toggle {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) !important;
}

.leaflet-control-layers-expanded {
  padding: 0.5rem !important;
}

.leaflet-control-layers label {
  display: flex !important;
  align-items: center !important;
  gap: 0.5rem !important;
  margin-bottom: 0.5rem !important;
  cursor: pointer !important;
}

.leaflet-control-layers-separator {
  margin: 0.5rem 0 !important;
  border-top: 1px solid var(--border-color) !important;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeOutDown {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(20px);
  }
}

.main-content {
  view-transition-name: main-content;
}

::view-transition-old(main-content) {
  animation: 300ms cubic-bezier(0.4, 0, 0.2, 1) both fadeOutDown;
}

::view-transition-new(main-content) {
  animation: 300ms cubic-bezier(0.4, 0, 0.2, 1) both fadeInUp;
}

@media screen and (max-width: 999px) {
  .navigation-drawer {
    position: fixed;
    top: 0;
    left: 0;
    width: 280px;
    height: 100vh;
    background: white;
    box-shadow: 2px 0 10px rgba(0,0,0,0.1);
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
    z-index: 1000;
    padding-top: 80px;
    overflow-y: auto;
  }

  .navigation-drawer.open {
    transform: translateX(0);
  }

  .nav-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,0.5);
    z-index: 999;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s, visibility 0.3s;
  }

  .nav-overlay.active {
    opacity: 1;
    visibility: visible;
  }

  body.menu-open {
    overflow: hidden;
    position: fixed;
    width: 100%;
  }
}

@media screen and (min-width: 1000px) {
  .navigation-drawer {
    position: static;
    width: auto;
    height: auto;
    transform: none !important;
    padding-top: 0;
    box-shadow: none;
  }
  
  .nav-list {
    display: flex;
    gap: 1rem;
  }
}

.bookmarks-header {
  margin-bottom: 2rem;
}

.bookmarks-header h1 {
  font-size: 2rem;
  color: var(--primary-color);
}

.remove-bookmark {
  margin-top: 1rem;
  width: 100%;
}

.story-actions {
  display: flex;
  gap: 1rem;
  margin-top: 1.5rem;
