g-inline: 1rem;\n  margin-inline: auto;\n  max-width: 1200px;\n}\n\nheader {\n  background-color: white;\n  box-shadow: var(--shadow-sm);\n  position: sticky;\n  top: 0;\n  z-index: 1000;\n}\n\n.main-header {\n  min-height: 80px;\n  padding-block: 1rem;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  gap: 1.5rem;\n}\n\n.brand-name {\n  font-size: 1.5rem;\n  font-weight: 700;\n  text-decoration: none;\n  color: var(--primary-color);\n  display: flex;\n  align-items: center;\n  gap: 0.5rem;\n}\n\n.brand-name i {\n  font-size: 1.25em;\n}\n\n.navigation-drawer {\n  min-height: 100vh;\n  width: 280px;\n  padding: 1.5rem 1rem;\n  background-color: white;\n  position: fixed;\n  top: 0;\n  left: 0;\n  box-shadow: var(--shadow-lg);\n  transform: translateX(-100%);\n  transition: transform 350ms ease-in-out;\n  z-index: 9999;\n}\n\n.navigation-drawer.open {\n  transform: translateX(0);\n}\n\n.drawer-button {\n  padding: 0.5rem;\n  border: 0;\n  border-radius: var(--radius-sm);\n  display: inline-block;\n  background-color: transparent;\n  font-weight: bold;\n  font-size: 1.5rem;\n  color: var(--text-color);\n  cursor: pointer;\n  transition: all 0.2s;\n}\n\n.drawer-button:hover {\n  background-color: var(--bg-color);\n}\n\n.nav-list {\n  list-style: none;\n  display: flex;\n  flex-direction: column;\n  gap: 0.5rem;\n  margin-top: 1.5rem;\n}\n\n.nav-list li a {\n  padding: 0.75rem 1rem;\n  display: block;\n  color: var(--text-color);\n  text-decoration: none;\n  border-radius: var(--radius-sm);\n  transition: all 0.2s;\n  display: flex;\n  align-items: center;\n  gap: 0.75rem;\n}\n\n.nav-list li a:hover {\n  background-color: var(--bg-color);\n  color: var(--primary-color);\n}\n\n.nav-list li a i {\n  width: 1.25rem;\n  text-align: center;\n}\n\n.main-content {\n  padding-block: 2rem;\n  min-height: calc(100vh - 80px);\n}\n\n.skip-link {\n  position: absolute;\n  top: -40px;\n  left: 0;\n  background: var(--primary-color);\n  color: white;\n  pa